import React, { useState, useEffect } from "react";

interface SlideshowProps {
  children: React.ReactNode[]; //   React Node 
  interval?: number; //    (ms ), undefined    
  showIndicators?: boolean; //    
  minHeight?: number; //   ( px )
}

export const Slideshow: React.FC<SlideshowProps> = ({
  children,
  interval,
  showIndicators = true,
  minHeight = 300, //  300px
}) => {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [currentMinHeight, setCurrentMinHeight] = useState(minHeight);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPaused, setIsPaused] = useState(false);
  const totalSlides = children.length;

  //      
  useEffect(() => {
    const updateWidth = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener("resize", updateWidth);
    updateWidth(); //       

    return () => window.removeEventListener("resize", updateWidth);
  }, []);

  //  minHeight  
  useEffect(() => {
    if (windowWidth < 768) {
      setCurrentMinHeight(minHeight + 100); //  +100px 
    } else {
      setCurrentMinHeight(minHeight);
    }
  }, [windowWidth, minHeight]);

  //    ()
  useEffect(() => {
    console.log(" minHeight :", currentMinHeight);
  }, [currentMinHeight]);

  //     (interval  )
  useEffect(() => {
    if (!interval || isPaused) return;

    const timer = setInterval(() => {
      setCurrentIndex((prevIndex) => (prevIndex + 1) % totalSlides);
    }, interval);

    return () => clearInterval(timer);
  }, [currentIndex, interval, isPaused, totalSlides]);

  //    
  const goToSlide = (index: number) => {
    setCurrentIndex(index);
  };

  return (
    <div
      className="w-full mx-auto"
      onMouseEnter={() => setIsPaused(true)}
      onMouseLeave={() => setIsPaused(false)}
    >
      {/*   (minHeight  ) */}
      <div
        className="relative w-full h-auto overflow-hidden transition-all duration-500"
        style={{ minHeight: `${currentMinHeight}px` }}
      >
        {React.Children.map(children, (child, index) => (
          <div
            key={index}
            className={`absolute inset-0 w-full h-full flex flex-col md:flex-row transition-opacity duration-1000 ${
              index === currentIndex ? "opacity-100 z-10" : "opacity-0 z-0"
            }`}
          >
            {child}
          </div>
        ))}
      </div>

      {/* showIndicators true    */}
      {showIndicators && (
        <div className="flex justify-center mt-2 space-x-2">
          {children.map((_, index) => (
            <button
              key={index}
              onClick={() => goToSlide(index)}
              className={`w-3 h-3 rounded-full ${
                index === currentIndex
                  ? "bg-orange-500"
                  : "bg-neutral-200 hover:bg-neutral-400"
              }`}
            />
          ))}
        </div>
      )}
    </div>
  );
};
